@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
.card {
  font-size: 9px;
  width: 10em;
  height: 16em;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 1px 1px 3px 1px #888;
  padding: 1.3em;
  background: #ebebeb;
  padding-bottom: 2em;
  position: relative;
}
.card__content {
  width: auto;
  height: 13em;
}
.card__content__img {
  display: block;
  margin: auto;
  width: auto;
  height: 80%;
}
.card__content__text {
  text-align: center;
  padding-top: 1em;
  font-family: sans-serif;
  font-family: "Roboto", sans-serif;
}

.card::after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.01);
  left: 0;
  top: 0;
}

@media only screen and (min-width: 350px) {
  .card {
    font-size: 11px;
  }
}
@media only screen and (min-width: 440px) {
  .card {
    font-size: 14px;
  }
}
@media only screen and (min-width: 600px) {
  .card {
    font-size: 18px;
  }
}
@media only screen and (min-width: 1700px) {
  .card {
    font-size: 23px;
  }
}

